  const colorMap = {
    0: "grey", //等待
    1: "#3498db", // 判题
    2: "#52c41a", // 成功
    3: "#262626", // 机器崩溃
    4: "#e74c3c", // 错误
  };
  return colorMap[status] || colorMap.default;
};
